.pseudoHeader {
  width: 100%;
  height: 100px;
}

.mainHeader {
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: center;
  margin: 35px 0;
  gap: 10%;

  .mainHeaderItem {
    width: 300px;
    height: 100%;
    font-size: 30px;
    letter-spacing: 3px;
    text-align: center;
    background-color: rgba(0, 0, 0, 0);
    color: #feedf3;
    border: none;

    &:hover {
      background-color: rgba(94, 5, 27, 0.42);
    }

  }
}

.mainSections {
  width: 100%;
  display: flex;
  gap: 50px;
}